	import TopHeader from '../../components/TopHead.vue'
	import ButtonItems from '../../components/ButtonItems.vue'
	import FooterMark from '../../components/FooterMark.vue'
	
    export default {
		data() {
			return {
				background: ['color1', 'color2', 'color3'],
				indicatorDots: false,
				autoplay: false,
				interval: 2000,
				duration: 500,
				marginwt: '0upx',
				userInfo:{},
				loginState: false,
				recommendData: {}
			}
		},
		components:{
			TopHeader,
			ButtonItems,
			FooterMark
		},
        onLoad() {
			this.getUserInfo(),
			this.getRecommendData()
        },
		methods:{
			openStorage: function(st){
				switch (st) {
					case 'reg':
						uni.navigateTo({url:'/pages/user/login'})
						break 
					case 'open': 
						uni.navigateTo({url:'/pages/lend/openStorage'})
						break
					default:
						uni.navigateTo({url:'/pages/user/login'})
				}
				
			},
			
			getUserInfo: function(){
				const userInfo = uni.getStorageSync('userInfo')
				
				if (userInfo){
					this.userInfo = userInfo
					this.loginState = true
					return false;
				}
				
				this.request({
					url: `consumer/my/consumers`,
				}).then(res => {
					this.userInfo = res.data.result
					this.loginState = true
					uni.setStorage({key:'userInfo', data: res.data.result})
				}).catch(err => {
					uni.showToast({
                        icon: 'none',
                        title: '用户信息获取失败，重新登录！'
                    });
				})
			},
			
			getRecommendData:function(){
				this.request({
					url: `transaction/projects/q`,
					params: {up:1},
					method: 'POST'
				}).then(res => {
					this.recommendData = res.data.result.content[0]
				}).catch(err => {
					uni.showToast({
                        icon: 'none',
                        title: '信息获取失败，请稍后重试！'
                    });
				})
			}
		}
    }
